<template>
  <div class=".content-container tracker-container">
    <div class="sort-bar">
      <div class="top">
        <div class="spacer-grow">&nbsp;</div>
        <div class="search">
          <input type="search" name="tsearch" id="tsearch" v-model="searchText" placeholder="search" @change="searchOrders">
        </div>
      </div>
      <div class="bottom">
        <ul>
          <li class="sortable unselectable" @click="colSort('num')"><p>num</p><div v-if="this.sortFlags.column == 'num'" id="el_sort_num" class="sort-icon"></div></li>
          <li class="sortable unselectable" @click="colSort('written')"><p>written</p><div v-if="this.sortFlags.column == 'written'" id="el_sort_written" class="sort-icon dn-arrow"></div></li>
          <li class="sortable unselectable" @click="colSort('due')"><p>due</p><div v-if="this.sortFlags.column == 'due'" id="el_sort_due" class="sort-icon"></div></li>
          <li class="sortable unselectable" @click="colSort('customer')"><p>customer</p><div v-if="this.sortFlags.column == 'customer'" id="el_sort_customer" class="sort-icon"></div></li>
          <li class="no-sort unselectable"><p>status</p></li>
          <li class="no-sort unselectable"><p>priority</p></li>
          <li class="sortable unselectable" @click="colSort('artist')"><p>artist</p><div v-if="sortFlags.column == 'artist'" id="el_artist_dept" class="sort-icon"></div></li>
          <li class="sortable unselectable" @click="colSort('location')"><p>location</p><div v-if="sortFlags.column == 'location'" id="el_location_dept" class="sort-icon"></div></li>
        </ul>
      </div>
    </div>
    <order v-for="(order, index) in filterOrders" :key="order.num" :order="order" :editing="false" class="order-item" :setEven="index" v-on:remove-order="refreshArchList(order.num)"></order>
    <div class="spacer-bottom">&nbsp;</div>
  </div>
</template>

<script>
import { bus } from './../main.js';
import Order from "./components/Order";
import firebase from "./../main.js";
import sort from 'fast-sort';

export default {
    name: 'archive',
    components: {
        Order
    },
    data(){
      return {
        aOrders: null,
        sortFlags: {
          column: 'num',
          direction: 'asc'
        },
        searchText: ''
      }
    },
    created() {
        //console.log("loading orders...");
        let that = this;
        //this.$store.dispatch('dbUpdate');
        this.$store.dispatch('getEmployees')
        .then(function(){
          that.$store.dispatch('getArchive')
          .then(archOrders => {
            that.archOrders = that.$store.getters.archOrders;
          });
        });        
    },
    computed: {
        filterOrders() {
          let searchResult = [];

          if(this.searchText == '') return this.$store.getters.archOrders;
          let initOrders = this.$store.getters.archOrders;
          let sText = this.searchText.toLowerCase();

          initOrders.forEach(order => {
            let num = order.num.toLowerCase();
            let customer = order.customer.toLowerCase();
            let notes = order.notes.toLowerCase();
            if(
              num.includes(sText) ||
              customer.includes(sText) ||
              notes.includes(sText)
            ) {
              searchResult.push(order);
            }
          })

          return searchResult;
        },
    },
    methods: {
      refreshList(num){
        this.$store.dispatch('removeArchOrder', num);
      },
      filterList(filters) {
        
      },
      setFilter(col, dir){

      },
      colSort(col) {
        let elId = "el_sort_" + this.sortFlags.column;
        let el = document.getElementById(elId);

        if(col == this.sortFlags.column){
          if(this.sortFlags.direction == 'asc'){
            this.sortFlags.direction = 'desc';
          } else if(this.sortFlags.direction == 'desc'){
            this.sortFlags.direction = 'asc';
          }
        } else {
          this.sortFlags.column = col;
          this.sortFlags.direction = 'asc';
        }

        switch(this.sortFlags.direction){
          case 'asc':
            if(el.classList.contains('dn-arrow')){
              // do nothing
            } else {
              if(el.classList.contains('up-arrow')){
                el.classList.remove('up-arrow');
              }
              el.classList.add('dn-arrow');
            }
            break;
          case 'desc':
            if(el.classList.contains('up-arrow')){
              // do nothing
            } else {
              if(el.classList.contains('dn-arrow')){
                el.classList.remove('dn-arrow');
              }
              el.classList.add('up-arrow');
            }
            break;
        }

        switch(this.sortFlags.column){
          case 'num':
            this.sortFlags.direction == 'asc' ? sort(this.aOrders).asc(o => o.num) : sort(this.aOrders).desc(o => o.num);
            break;
          
          case 'written':
            this.sortFlags.direction == 'asc' ? sort(this.aOrders).asc(o => o.written) : sort(this.aOrders).desc(o => o.written);
            break;
          
          case 'due':
            this.sortFlags.direction == 'asc' ? sort(this.aOrders).asc(o => o.due) : sort(this.aOrders).desc(o => o.due);
            break;
          
          case 'customer':
            this.sortFlags.direction == 'asc' ? sort(this.aOrders).asc(o => o.customer) : sort(this.aOrders).desc(o => o.customer);
            break;
          
          case 'artist':
            this.sortFlags.direction == 'asc' ? sort(this.aOrders).asc(o => o.uid) : sort(this.aOrders).desc(o => o.uid);
            break;

          case 'location':
            this.sortFlags.direction == 'asc' ? sort(this.aOrders).asc(o => o.uid) : sort(this.aOrders).desc(o => o.uid);
            break;
        }
        
      },
      searchOrders() {

      }
    }
}
</script>