import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import { firebase } from "./main.js";
import { bus } from "./main.js";
import { stat } from 'fs';
import router from './router.js';

Vue.use(Vuex)
axios.defaults.baseURL = 'http://tsot.work/api'
axios.defaults.headers.post['Access-Control-Allow-Origin'] = '*';

export default new Vuex.Store({
  state: {
    token: localStorage.getItem('access_token') || null,
    user: null,
    userInfo: {
      dept: 'none',
      role: 'user'
    },
    bLoggedIn: false,
    orders: [],
    archOrders: [],
    filter: 'all',
    statuses: [
      {val: "0", name: "Not Started"},
      {val: "1", name: "Proofing"},
      {val: "2", name: "Production"},
      {val: "3", name: "Application"},
      {val: "4", name: "Complete"},
      {val: "5", name: "Archived"}
    ],
    priorities: [
      {val: "0", name: "Standard"},
      {val: "1", name: "Rush"}
    ],
    shipping: [
      {val: "0", name: "Pick up"},
      {val: "1", name: "Ship"},
      {val: "2", name: "Delivery"},
      {val: "3", name: "Install"}
    ],
    employees: [
      // filled later
    ]
  },
  getters: {
    statuses(state) {
      return state.statuses;
    },
    priorities (state) {
      return state.priorities;
    },
    shipping (state) {
      return state.shipping;
    },
    orders(state) {
      return state.orders;
    },
    archOrders(state) {
      return state.archOrders;
    },
    userName (state) {
      if(state.user){
        return state.user.displayName;
      } else {
        return "Guest";
      }
    },
    userRole(state){
      return state.userInfo.role;
    },
    userDept(state){
      return state.userInfo.dept;
    },
    loggedIn(state){
      return state.bLoggedIn;
    },
    isAdmin (state) {
      return state.userInfo.role == 'admin';
    },
    isArt (state){
      return state.userInfo.dept == 'art';
    },
    isApp (state) {
      return state.userInfo.dept == 'app';
    },
    employees(state) {
      return state.employees;
    }
  },
  mutations: {
    setOrders(state, orders){
      state.orders = orders;
    },
    setArchive(state, orders){
      state.archOrders = orders;
    },
    submitOrder(state, data) {
      console.log("Order Submitted");
    },
    setUser(state, user) {
      state.user = user;
      state.bLoggedIn = true;
      router.push({name: 'tracker'});
    },
    resetUser(state) {
      state.user = null;
      state.userInfo.dept = 'none';
      state.userInfo.role = 'user';
      state.bLoggedIn = false;
      router.push({name: 'login'});
    },
    setUserInfo(state, info){
      state.userInfo.role = info.role;
      state.userInfo.dept = info.dept;
    },
    removeOrder(state, num) {
      console.log(`removing order: ${num}`);
      for(var i = 0; i < state.orders.length; i++){
        if(state.orders[i].num == num){
          Vue.delete(state.orders, i);
        }
      }
    },
    deleteOrder(state, num){
      console.log(`deleting order: ${num}`);
      for(var i = 0; i < state.orders.length; i++){
        if(state.orders[i].num == num){
          firebase.db.collection("orders").doc(num).delete();
          Vue.delete(state.orders, i);
        }
      }
    },
    setEmployees(state, emps){
      state.employees = emps;
    }
  },
  actions: {
    async getOrders(context) {
      if(firebase.auth.currentUser){
        let orders = [];
        firebase.db.collection("orders").get().then(qs => {
          qs.forEach(doc => {
            if(!doc.data().archived){
              if(doc.data().location == firebase.auth.currentUser.uid || this.state.userInfo.role == "admin"){
                orders.push(doc.data());
              }
            }
          })
        });
        context.commit('setOrders', orders);
      }
    },
    async getArchive(context) {
      if(firebase.auth.currentUser){
        let aOrders = [];
        firebase.db.collection("orders").get().then(qs => {
          qs.forEach(doc => {
            if(doc.data().archived){
              if(this.state.userInfo.role == "admin"){
                aOrders.push(doc.data());
              }
            }
          })
        });
        context.commit('setArchive', aOrders);
      }
    },
    async getOrdersFiltered(context, filters) {
      if(firebase.auth.currentUser){
        let orders = [];
        let result = firebase.db.collection("orders").get().then(qs => {
          qs.forEach(doc => {
            orders.push(doc.data());
          })
        });
        context.commit('setOrders', orders);
      }
    },
    submitOrder(context, data) {
      //console.log(data.num);
      let exists = firebase.db.collection("orders").where("num", "==", data.num).get()
      .then(response => {
        if(response.docs.length == 0){ // order doesn't exist in db
          firebase.db.collection('orders').doc(data.num).set(data)
          .then(function(){
            //
          })
        }
      });
      //let o = firebase.db.collection("orders").doc(data.num).set(data);
    },
    removeOrder(context, num) {
        context.commit('removeOrder', num);
    },
    updateOrder(context, data) {
      firebase.db.collection('orders').doc(data.num).update(data);
    },

    // TEMPORARY //
    dbUpdate(context){
      let batch = firebase.db.batch();
      firebase.db.collection('orders').where('artist', '==', '').get()
      .then(qs => {
        qs.forEach(doc => {
            console.log(doc.data().uid);
            let a = doc.data().uid;
            let l = doc.data().uid;

            if(doc.data().uid == "maxGYo8GjsYNd5upKnFAAOCOlK83"){
              a = "1Gm1StyFamObfzPOhMNkv4u14QV2";
            }

            batch.update(
              firebase.db.collection('orders').doc(doc.data().num),
              {
                "artist": a,
                "location": l
              }
            );
        });
        batch.commit();
      }).then(function() {
        
      })
    },
    initializeUser(context){
      if(!firebase.auth.currentUser){ // Not authenticated
        context.commit('resetUser');
      } else {
        //console.log(firebase.auth.currentUser);
        let result = firebase.db.collection("roles").where('uid', '==', firebase.auth.currentUser.uid).get()
        .then(qs => {
          qs.forEach(doc => {
            context.commit('setUserInfo', {'role': doc.data().role, 'dept': doc.data().dept});
          })
        }).then(function() {
          context.commit('setUser', firebase.auth.currentUser);
        })
      }
    },
    deleteOrder(context, num){
      context.commit('deleteOrder', num);
    },
    register (context, data) {
      console.log(data);
      let n = data.name;
      firebase.auth.createUserWithEmailAndPassword(data.email, data.password)
      .then(
        response => {
          let u = response.user;
          return u.updateProfile({displayName: n});
        },
        error => {
          console.error(error.message);
        }).then(function(){
          firebase.db.collection('roles').add({'uid': firebase.auth.currentUser.uid, 'role': 'user', 'dept': 'app', 'name': n})
          .then(function(){
            context.commit('setUserInfo', {'role': doc.data().role, 'dept': doc.data().dept});
          })
        })
    },
    logout(context) {
      if(firebase.auth.currentUser)
        firebase.auth.signOut();
    },
    login(context, credentials) {
      firebase.auth.signInWithEmailAndPassword(credentials.email, credentials.password)
      .then(response => {
        //
      }).then(function(){
        let result = firebase.db.collection("roles").where('uid', '==', firebase.auth.currentUser.uid).get()
        .then(qs => {
          qs.forEach(doc => {
            context.commit('setUserInfo', {'role': doc.data().role, 'dept': doc.data().dept});
          })
        });
      })
    },
    setUser(context, user){
      context.commit("setUser", user);
    },
    resetUser(context){
      context.commit("resetUser");
    },
    getEmployees(context){
      firebase.db.collection('roles').get()
      .then(qs => {
        let emps = [];
        qs.forEach(doc => {
          emps.push({'uid': doc.data().uid, 'name': doc.data().name, 'dept': doc.data().dept, 'role': doc.data().role});
        });
        context.commit('setEmployees', emps);
      });
    }
  }
})
