<template>
    <div class="content-container form-container">
        <p class="mt-20">Create Order</p>
        <form action="#" @submit.prevent="validateBeforeSubmit">
            <div class="form-row">
                <p class="label">Number</p>
                <input type="text" name="num" v-model="num" placeholder="00000">
            </div>
            <div class="form-row">
                <p class="label">Customer</p>
                <input type="text" name="name" v-model="customer" placeholder="name">
            </div>
            <div class="form-row">
                <p class="label">Written</p>
                <!-- <datepicker name="written" v-model="written" placeholder="2019-06-21" :format="customFormatter"></datepicker> -->
                <datepicker name="written" v-model="written" placeholder="2019-06-21" :format="customFormatter"></datepicker>
            </div>
            <div class="form-row">
                <p class="label">Due</p>
                <!-- <datepicker name="due" v-model="due" placeholder="2019-06-28" :format="customFormatter"></datepicker> -->
                <datepicker name="due" v-model="due" placeholder="2019-06-28" :format="customFormatter"></datepicker>
            </div>
            <div class="form-row">
                <p class="label">Shipping</p>
                <select name="ship" v-model="ship">
                    <option v-for="ship in shipping" :key="ship.val" :value="ship.val">{{ ship.name }}</option>
                </select>
            </div>
            <div class="form-row">
                <p class="label">Assign</p>
                <select name="ship" v-model="uid">
                    <option v-for="emp in filteredEmployees" :key="emp.uid" :value="emp.uid">{{ emp.name }}</option>
                </select>
            </div>
            <div class="form-row">
                <p class="label">Status</p>
                <select name="status" v-model="status">
                    <option v-for="stat in statuses" :key="stat.val" :value="stat.val">{{ stat.name }}</option>
                </select>
            </div>
            <div class="form-row">
                <p class="label">Rush</p>
                <input type="checkbox" class="tickbox" name="rush" @change="toggleCheckbox('rush')" />
            </div>
            <div class="form-row">
                <p class="label">Notes</p>
                <textarea name="notes" v-model="notes" rows="3"></textarea>
            </div>

            <div class="form-row">
                <div class="spacer-grow">&nbsp;</div>
                <input type="submit" class="btn-submit">
            </div>

            <div v-if="formErrors.length > 0" class="form-row">
                <div class="spacer-grow">&nbsp;</div>
                <p v-for="e in formErrors" :key="e" class="error-text">{{ e }}</p>
            </div>
        </form>
    </div>
</template>

<script>
import { bus } from './../main.js';
import Datepicker from 'vuejs-datepicker/dist/vuejs-datepicker.js';
import * as lang from 'vuejs-datepicker/src/locale';
import moment from 'moment';

export default {
    name: "create-order",
    components: {
        Datepicker
    },
    data() {
        return {
            order: null,
            num: '',
            customer: '',
            written: '',
            due: '',
            ship: '0',
            status: '0',
            rush: false,
            notes: '',
            archived: false,
            uid: 'a9Fh8uBiUcXaGJIBIn7hOWojm6f2', // placeholder
            formErrors: []
        }
    },
    computed: {
        filteredEmployees(){
            let filtered = [];
            let unfiltered = this.$store.getters.employees;
            let nobody = {};
            for(let i = 0; i < unfiltered.length; i++){
                if(unfiltered[i].uid == 'a9Fh8uBiUcXaGJIBIn7hOWojm6f2'){
                    filtered.push(unfiltered[i]);
                }
            }
            unfiltered.forEach(e => {
                if(e.uid != 'a9Fh8uBiUcXaGJIBIn7hOWojm6f2' && e.uid != 'z1eFNGZQC2bBnET7SYfvdyIyfJ62'){
                    filtered.push(e);
                }
            })
            return filtered;
        },
        employees(){
            return this.$store.getters.employees;
        },
        shipping(){
            return this.$store.getters.shipping;
        },
        statuses(){
            return this.$store.getters.statuses;
        }
    },
    methods: {
        customFormatter(date){ 
            let formatted = moment(date).format('YYYY-MM-DD');
            return formatted;
        },
        setDateWritten(e){
            this.written = this.customFormatter(e);
        },
        setDateDue(e){
            this.due = this.customFormatter(e);
        },
        validateBeforeSubmit(){
            console.log("Validating...");
            this.formErrors = [];
            let data = {
                archived: false,
                num: this.num,
                customer: this.customer,
                written: this.customFormatter(this.written),
                due: this.customFormatter(this.due),
                shiptype: this.ship,
                status: this.status,
                rush: this.rush,
                artist: this.uid,
                location: this.uid,
                notes: this.notes
            }

            if( data.num == "" ||
                data.customer == "" ||
                data.written == "Invalid date" ||
                data.due == "Invalid date" ) {
                this.formErrors.push("Please fill out all required fields.");
            }

            if(this.formErrors.length == 0){
                this.create(data);
            } else {
                console.log(`Errors: ${this.formErrors}`);
                console.log("Form Data: ");
                console.log(data);
            }
            
        },
        create(data) {
            this.$store.dispatch('submitOrder', data)
            .then(response => {
                this.$router.push({ name: 'tracker' });
            })
            .catch(error => {
                //this.serverErrors = Object.values(error.response.data.errors)
            })
        },
        toggleCheckbox(el){
            if(el == 'rush'){
                if(this.rush == false){
                    this.rush = true;
                } else if(this.rush == true){
                    this.rush = false;
                }
            }
        }
    }
}
</script>