<template>
    <div :class="[isEven ? 'bg-dark' : 'bg-light'] + ' order-container order_' + order.num">
        <div class="top">
            <ul>
                <!-- <li class="num"><p>{{ order.num }}</p></li> -->
                <li v-if="isAdmin" class="num" @dblclick="editOrder(order.id, 'num')">
                    <p v-if="editing != 'num'">{{ inum }}</p>
                    <input v-else-if="editing == 'num'" type="text" @blur="doneEdit(order, 'num')" v-model="inum" @keyup.enter="doneEdit(order, 'num')" @keyup.esc="doneEdit(order, 'num')" v-focus>
                </li>
                <li v-if="!isAdmin" class="num">
                    <p v-if="editing != 'num'">{{ inum }}</p>
                    <input v-else-if="editing == 'num'" type="text" @blur="doneEdit(order, 'num')" v-model="inum" @keyup.enter="doneEdit(order, 'num')" @keyup.esc="doneEdit(order, 'num')" v-focus>
                </li>
                <li class="dates">
                    <div class="written" @dblclick="editOrder(order.id, 'written')">
                        <p class="label">Written:</p>
                        <p v-if="!isAdmin" class="date">{{ order.written }}</p>
                        <div v-if="isAdmin" :class="[isEven ? 'dateDropdown-dk' : 'dateDropdown-lt']">
                            <datepicker :placeholder="order.written" :format="customFormatter" @selected="setDateWritten"></datepicker>
                        </div>
                    </div>
                    <div class="due">
                        <p class="label">Due:</p>
                        <p v-if="!isAdmin" class="date">{{ order.due }}</p>
                        <div v-if="isAdmin" :class="[isEven ? 'dateDropdown-dk' : 'dateDropdown-lt']">
                            <datepicker :placeholder="order.due" :format="customFormatter" @selected="setDateDue"></datepicker>
                        </div>
                    </div>
                </li>
                <li v-if="isAdmin" class="customer" @dblclick="editOrder(order.id, 'customer')">
                    <p v-if="editing != 'customer'">{{ icustomer }}</p>
                    <input v-else-if="editing == 'customer'" type="text" @blur="doneEdit(order, 'customer')" v-model="icustomer" @keyup.enter="doneEdit(order, 'customer')" @keyup.esc="doneEdit(order, 'customer')" v-focus>
                </li>
                <li v-if="!isAdmin" class="customer">
                    <p v-if="editing != 'customer'">{{ icustomer }}</p>
                    <input v-else-if="editing == 'customer'" type="text" @blur="doneEdit(order, 'customer')" v-model="icustomer" @keyup.enter="doneEdit(order, 'customer')" @keyup.esc="doneEdit(order, 'customer')" v-focus>
                </li>
                <li class="status"><p class='status'>{{ curStatus }}</p></li>
                <li class="flags">
                    <p class="ship">{{ orderShipping }}</p>
                    <p class="priority">{{ orderPriority }}</p>
                </li>
                <li class="status">
                    <p>{{ orderArtist }}</p>
                </li>
                <li class="user">
                    <p>{{ orderLocation }}</p>
                </li>
            </ul>
        </div>
        <div class="bottom">
            <div class="notes" @dblclick="editOrder(order.id, 'notes')">
                <p v-if="editing != 'notes'">{{ inotes }}</p>
                <input v-else-if="editing == 'notes'" type="text" @blur="doneEdit(order, 'notes')" v-model="inotes" @keyup.enter="doneEdit(order, 'notes')" @keyup.esc="doneEdit(order, 'notes')" v-focus>
            </div>
            <div v-if="!isArchived" class="interface">
                <div class="menu-status submit-btn">
                    <div class="dropdown" v-if="isArt || isAdmin"><p>SET STATUS</p>
                        <ul class="dropdown-content">
                            <li v-for="stat in statuses" :key="stat.val" :value="stat.val" @click="setOrderStatus(order, stat.val)"><p>{{ stat.name }}</p></li>
                        </ul>
                    </div>
                </div>
                <div class="menu-status submit-btn">
                    <div class="dropdown" v-if="isAdmin"><p>ARTIST</p>
                        <ul class="dropdown-content">
                            <li v-for="emp in filteredEmployees" :key="emp.uid" :value="emp.uid" @click="assignArtist(order, emp.uid)"><p>{{ emp.name }}</p></li>
                        </ul>
                    </div>
                </div>
                <div class="menu-status submit-btn">
                    <div class="dropdown" v-if="isAdmin"><p>LOCATION</p>
                        <ul class="dropdown-content">
                            <li v-for="emp in filteredEmployees" :key="emp.uid" :value="emp.uid" @click="assignLocation(order, emp.uid)"><p>{{ emp.name }}</p></li>
                        </ul>
                    </div>
                </div>
                <div v-if="isArt && !isAdmin" class="submit-btn" @click="submitToApp(order)"><p>SEND TO APP</p></div>
                <div v-if="isArt || isApp || isAdmin" class="submit-btn" @click="submitToArchive(order)"><p>FINISH</p></div>
                <div v-if="isAdmin" class="submit-btn" @click="deleteOrder(order)"><p>DELETE</p></div>
            </div>
        </div>
    </div>
</template>

<script>
import { bus } from './../../main.js';
import Datepicker from 'vuejs-datepicker/dist/vuejs-datepicker.js';
import * as lang from 'vuejs-datepicker/src/locale';
import moment from 'moment';

export default {
    name: 'order',
    that: this,
    props: {
        order: {
            type: Object,
            required: true,
        },
        setEven: {
            type: Number,
            required: true
        }
    },
    components: {
        Datepicker
    },
    data() {
        return {
            editing: 'none',
            inum: this.order.num,
            icustomer: this.order.customer,
            inotes: this.order.notes,
            cache: {
                num: '',
                customer: '',
                notes: ''
            },

            // date picker
            format: "YYYY-MM-DD",
            disabledDates: {},
            disabledFn: {
                customPredictor(date) {
                    if(date.getDate() % 3 == 0) {
                        return true;
                    }
                }
            },
            highlightedFn: {
                customPredictor(date) {
                    if(date.getDate() % 4 == 0) {
                        return true;
                    }
                }
            },
            highlighted: {},
            eventMsg: null,
            language: 'en',
            languages: lang,
            vModelExample: null,
            changedMonthLog: [],
            mStatus: 0
        }
    },
    created() {
        //console.log(this.order.written);
        this.getOrderStatus(this.order.status);
        this.icustomer = this.order.customer;
        this.inotes = this.order.notes;
    },
    computed: {
        isAdmin(){
            return this.$store.getters.isAdmin;
        },
        isApp(){
            return this.$store.getters.isApp;
        },
        isArt(){
            return this.$store.getters.isArt;
        },
        isEven(){
            return this.setEven % 2 == 0;
        },
        isArchived(){
            return this.order.archived;
        },
        statuses(){
            return this.$store.getters.statuses;
        },
        employees(){
            return this.$store.getters.employees;
        },
        filteredEmployees(){
            let filtered = [];
            let unfiltered = this.$store.getters.employees;
            let nobody = {};
            for(let i = 0; i < unfiltered.length; i++){
                if(unfiltered[i].uid == 'a9Fh8uBiUcXaGJIBIn7hOWojm6f2'){
                    filtered.push(unfiltered[i]);
                }
            }
            unfiltered.forEach(e => {
                if(e.uid != 'a9Fh8uBiUcXaGJIBIn7hOWojm6f2' && e.uid != 'z1eFNGZQC2bBnET7SYfvdyIyfJ62'){
                    filtered.push(e);
                }
            })
            return filtered;
        },
        curStatus(){
            return this.$store.getters.statuses[this.order.status].name;
        },
        orderPriority() {
            return this.order.rush == false ? this.$store.getters.priorities[0].name : this.$store.getters.priorities[1].name;
        },
        orderShipping() {
            return this.$store.getters.shipping[this.order.shiptype].name;
        },
        orderUser() {
            let result = 'not found';
            for(let i = 0; i < this.$store.getters.employees.length; i++){
                if(this.$store.getters.employees[i].uid == this.order.uid){
                    result = this.$store.getters.employees[i].name
                }
            }
            return result;
        },
        orderArtist() {
            let result = 'not found';
            for(let i = 0; i < this.$store.getters.employees.length; i++){
                if(this.$store.getters.employees[i].uid == this.order.artist){
                    result = this.$store.getters.employees[i].name
                }
            }
            return result;
        },
        orderLocation() {
            let result = 'not found';
            for(let i = 0; i < this.$store.getters.employees.length; i++){
                if(this.$store.getters.employees[i].uid == this.order.location){
                    result = this.$store.getters.employees[i].name
                }
            }
            return result;
        }
    },
    directives: {
        focus: {
            inserted: function(el) {
                el.focus();
            }
        }
    },
    methods: {
        submitToApp(order) {
            order.location = 'maxGYo8GjsYNd5upKnFAAOCOlK83'; // production
            this.updateOrder(order);
            this.$emit('remove-order');
        },
        submitToArchive(order) {
            order.archived = true;
            this.updateOrder(order);
            this.$emit('remove-order');
        },
        setOrderStatus(order, status){
            this.order.status = status;

            this.updateOrder(order);
            return this.order.status;
        },
        getOrderStatus(status){
            this.mStatus = this.$store.getters.statuses[status];
            return this.$store.getters.statuses[status];
        },
        assignLocation(order, uid) {
            order.location = uid;
            this.updateOrder(order);
        },
        assignArtist(order, uid) {
            order.artist = uid;
            this.updateOrder(order);
        },
        updateOrder(order) {
            let data = {
                archived: order.archived,
                num: order.num,
                customer: order.customer,
                written: this.customFormatter(order.written),
                due: this.customFormatter(order.due),
                shiptype: order.shiptype,
                status: order.status,
                rush: order.rush,
                artist: order.artist,
                location: order.location,
                notes: order.notes
            }
            this.$store.dispatch('updateOrder', data);
        },
        editOrder(id, prop) {
            switch(prop){
                case 'num':
                    this.cache.num = this.order.num;
                    this.editing = 'num';
                    break;

                case 'customer':
                    this.cache.customer = this.order.customer;
                    this.editing = 'customer';
                    break;

                case 'notes':
                    this.cache.notes = this.order.notes;
                    this.editing = 'notes';
                    break;
                
                case 'written':
                    this.editing = 'written';
                    break;
            }        
        },
        deleteOrder(order){
            this.$store.dispatch('deleteOrder', order.num);
        },
        doneEdit(order, prop){
            //console.log("finished");
            var bEdited = false;
            switch(prop){
                case 'num':
                    bEdited = true;
                    if(this.inum.trim() == '' || this.inum == this.cache.num){
                        this.inum = this.cache.num;
                        bEdited = false;
                    }
                    order.num = this.inum;
                    break;

                case 'customer':
                    bEdited = true;
                    if(this.icustomer.trim() == '' || this.icustomer == this.cache.customer){
                        this.icustomer = this.cache.customer;
                        bEdited = false;
                    }
                    order.customer = this.icustomer;
                    break;

                case 'notes':
                    bEdited = true;
                    if(this.inotes.trim() == '' || this.inotes == this.cache.notes){
                        this.inotes = this.cache.notes;
                        bEdited = false;
                    }
                    order.notes = this.inotes;
                    break;

                case 'written':
                    bEdited = true;
                    console.log("Finished Editing Written Date");
                    break;
            }
            if(bEdited){
                this.updateOrder(order);
            }

            this.editing = 'none';
        },
        openPicker(id){
            this.$refs.programaticOpen.showCalendar();
        },
        setDateWritten(e){
            //console.log(moment(e).format('YYYY-MM-DD'));
            this.order.written = moment(e).format('YYYY-MM-DD');
            this.updateOrder(this.order);
        },
        setDateDue(e){
            //console.log(moment(e).format('YYYY-MM-DD'));
            this.order.due = moment(e).format('YYYY-MM-DD');
            this.updateOrder(this.order);
        },
        customFormatter(date){ 
            let formatted = moment(date).format('YYYY-MM-DD');
            return formatted;
        }
    }
}
</script>