import Vue from 'vue'
import Router from 'vue-router'
import Tracker from './views/Tracker.vue'
import Archive from './views/Archive.vue'
import Login from './views/auth/Login.vue'
import Register from './views/auth/Register.vue'
import Logout from './views/auth/Logout'
import CreateOrder from './views/CreateOrder'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'tracker',
      component: Tracker,
      meta: {
        title: 'tsot',
        requiresAuth: true
      }
    },
    {
      path: '/archive',
      name: 'archive',
      component: Archive,
      meta: {
        title: 'tsot - archive',
        requiresAuth: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        title: 'tsot',
        requiresVisitor: true
      }
    },
    {
      path: '/register',
      name: 'register',
      component: Register,
      meta: {
        title: 'tsot',
        requiresVisitor: true
      }
    },
    {
      path: '/logout',
      name: 'logout',
      component: Logout,
      meta: {
        title: 'tsot',
        requiresAuth: true
      }
    },
    {
      path: '/create',
      name: 'create',
      component: CreateOrder,
      meta: {
        title: 'tsot',
        requiresAuth: true
      }
    }
  ]
})
